html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Palanquin Dark", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

/** Media Queries **/
.login {
  margin-top: 5rem;
}
.login .uptask {
  margin-bottom: 0;
  background: linear-gradient(to right, #1a1a1a 0%, #666666 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 9rem;
  }
}
.login .tagline {
  text-align: center;
  margin-top: 1rem;
}
.login input[type=submit] {
  background-color: #1a1a1a;
  padding: 1.5rem 4rem;
  color: white;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: black;
  cursor: pointer;
}
.login .acciones a {
  color: black;
}

