}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #f1f2f4;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  color: #000;
  margin-bottom: 2rem;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}
.acciones a:hover {
  color: gray;
}

.alerta {
  padding: 1rem;
  text-transform: uppercase;
  margin-top: 1rem;
  font-size: 1.4rem;
  max-width: 80rem;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
}
.alerta.error {
  background-color: #ffc3c3;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.error button {
  cursor: pointer;
  background-color: transparent;
  color: rgb(169, 0, 0);
}
.alerta.error button:hover {
  background-color: #ffa9a9;
}
.alerta.exito {
  background-color: #92ff92;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}
.alerta.exito button {
  cursor: pointer;
  background-color: transparent;
  color: rgb(0, 120, 0);
}
.alerta.exito button:hover {
  background-color: #78ff78;
}

@media (min-width: 768px) {
  .tabs {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1024px) {
  .tabs {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(5, 1fr);
  }
}
.tabs .botones {
  background-color: #000000;
  border: solid 1px #333333;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  font-weight: normal;
  transition: background-color 300ms ease;
}
.tabs .botones:hover {
  cursor: pointer;
  background-color: black;
}
.tabs .botones.actual {
  background-color: #FFFFFF;
  color: #000000;
}

