}

.sidebar {
  opacity: 0;
  background-color: #000000;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
  background-color: #000000;
}
.sidebar.mostrar {
  opacity: 1;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.95);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar.ocultar {
    height: 100%;
    opacity: 1;
  }
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
  }
}
.sidebar h2 {
  padding: 2rem 9rem 1rem 2rem;
  text-align: left;
  color: #FFFFFF;
  font-size: 6rem;
}
.sidebar .sidebar-nav {
  margin-top: 4rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  font-size: 1.7rem;
  padding: 1.4rem 0 1.4rem 2rem;
  transition-property: background-color;
  transition-duration: 100ms;
}
.sidebar .sidebar-nav a:nth-last-child(even):not(:first-of-type) {
  border: 1px solid #1a1a1a;
}
.sidebar .sidebar-nav a:hover:not(:first-of-type), .sidebar .sidebar-nav a.activo:not(:first-of-type) {
  background-color: #101215;
  border-left: 0.5rem solid #4B5563;
  color: #FFFFFF;
}
.sidebar .sidebar-nav .crear-proyecto {
  display: flex;
  justify-content: center;
  margin-bottom: 4rem;
  padding: 0;
}
.sidebar .sidebar-nav .crear-proyecto span {
  background-color: #6b7280;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  display: inline;
  border-radius: 2rem;
  padding: 1rem 2rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav .crear-proyecto span {
    width: auto;
  }
}
.sidebar .sidebar-nav .crear-proyecto span:hover {
  background-color: #545964;
  cursor: pointer;
}
