.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .formulario {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-out;
  opacity: 0;
}
.modal .formulario legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal .formulario .campo {
  margin-top: 2rem;
}
.modal .formulario input[type=text] {
  border: 1px solid #6b7280;
  border-radius: 0.5rem;
}
.modal .formulario .opciones {
  display: flex;
  justify-content: space-between;
}
.modal .formulario .submit-nueva-tarea {
  background-color: #000000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .submit-nueva-tarea {
    width: auto;
  }
}
.modal .formulario .submit-nueva-tarea:hover {
  background-color: black;
  cursor: pointer;
}
.modal .formulario .cerrar-modal {
  background-color: #000000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .cerrar-modal:hover {
  background-color: black;
  cursor: pointer;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-30rem);
  opacity: 0;
}

